<template>
    <div class="m-mint">
        <section class="m-mint-nav">
            <div class="container-fluid m-mint-nav-container">
                <router-link to="/">
                    <img class="m-mint-nav-logo" :src="images.mseaLogo" alt="MSEA">
                </router-link>
                <nav class="m-mint-nav-icons">
                    <a href="/mint" class="m-mint-nav-link">
                        <img class=" m-mint-nav-icon" :src="images.iconGithub" alt="Use Github to connect" />
                    </a>
                    <a href="/mint" class="m-mint-nav-link">
                        <img class=" m-mint-nav-icon" :src="images.iconTwitter" alt="Use Twitter to connect" />
                    </a>
                    <a href="/mint" class="m-mint-nav-link">
                        <img class=" m-mint-nav-icon" :src="images.iconFisher" alt="Use to connect" />
                    </a>
                    <a href="/mint" class="m-mint-nav-link">
                        <img class=" m-mint-nav-icon" :src="images.iconEmail" alt="Use E-mail to connect" />
                    </a>
                </nav>
                <div v-if="account" class="m-mint-nav-account">
                    {{ getAccount }}
                </div>
                <div v-else class="m-mint-nav-account" @click="selectWallet">
                    {{ $t('select_wallet') }}
                </div>
            </div>
        </section>
        <section class="m-mint-banner">
            <div class="container-fluid">
                <div class="m-mint-banner--content">
                    <div class="m-mint-banner--headline">
                        <div class="m-mint-banner--headline-logo">
                            <img :src="images.mseaIcon" alt="MSEA">
                        </div>
                        <h1 class="m-mint-banner--headline-title">
                            The first Game-props trading platform in the GameFi world
                        </h1>
                    </div>
                    <div class="m-mint-banner--box" v-if="stage === 0">
                        <div class="m-mint-banner--box-section m-mint-banner--info-list">
                            <div class="m-mint-banner--info-item">
                                <span class="m-mint-banner--info-label">Total</span>
                                <span class="m-mint-banner--info-value">10000</span>
                            </div>
                            <div class="m-mint-banner--info-item">
                                <span class="m-mint-banner--info-label">Price</span>
                                <span class="m-mint-banner--info-value">{{ type > 1 ? 0.125 : 0.1 }}eth</span>
                            </div>
                            <div class="m-mint-banner--info-item">
                                <span class="m-mint-banner--info-label">Limit</span>
                                <span class="m-mint-banner--info-value">{{ userLimit }}</span>
                            </div>
                        </div>
                        <div class="m-mint-banner--box-section m-mint-banner--brand">MSEA</div>
                        <div class="m-mint-banner--box-section m-mint-banner--calculator">
                            <div class="m-mint-banner--calculator-calc">
                                <span class="m-mint-banner--calculator-text">Quantity</span>
                                <span class="m-mint-banner--calculator-action minus" @click="calc(-1)">-</span>
                                <span class="m-mint-banner--calculator-text">{{ userAmount }}</span>
                                <span class="m-mint-banner--calculator-action plus" @click="calc(1)">+</span>
                            </div>
                            <p class="m-mint-banner--calculator-text">
                                {{ $t("price") }} :
                                {{ type > 1 ? (0.125 * userAmount) : (0.1 * userAmount) }} ETH</p>
                        </div>
                        <div class="m-mint-banner--box-section m-mint-banner--actions" v-if="!account">
                            <span class="m-mint-banner--action">{{ $t("select_wallet") }}</span>
                        </div>
                        <div class="m-mint-banner--box-section m-mint-banner--actions" v-else>
                            <div v-if="type > 0 && userLimit > 0">
                                <span class="m-mint-banner--action submit">
                                    {{
                                            confirmFinish == 2
                                                ? "Loading"
                                                : type == 1
                                                    ? $t("mint_whitelist")
                                                    : $t("mint_public")
                                    }}
                                </span>
                            </div>
                            <span v-else class="m-mint-banner--action disabled">{{ $t("mint_whitelist") }}</span>
                        </div>
                        <div class="m-mint-banner--box-section m-mint-banner--progress-wrap" v-if="account">
                            <p class="m-mint-banner--progress-labels">
                                <span class="m-mint-banner--progress-label">Tokens minted</span>
                                <span class="m-mint-banner--progress-label">
                                    {{ tokensMintedPercent }}%({{ tokensMinted }}/{{ tokensAmount }})
                                </span>
                            </p>
                            <div class="m-mint-banner--progress">
                                <div class="m-mint-banner--progress-inner"
                                    :style="{ width: `${tokensMinted / tokensAmount * 100}%` }"></div>
                            </div>
                        </div>
                    </div>
                    <div class="m-mint-banner--box" v-else>
                        <p class="m-mint-banner--stage-title">{{ stage === 1 ? $t("sold_out") : $t("finish") }}</p>
                        <p class="m-mint-banner--stage-desc">{{ $t("mint_info") }}</p>
                        <span class="m-mint-banner--stage-button" @click="goToOpensea">OPENSEA</span>
                    </div>
                </div>
            </div>
        </section>

        <section class="m-mint-modal" v-show="modal.visible > 0">
            <div class="m-mint-modal--wrapper">
                <div class="col-center m-mint-modal--content">
                    <div class="row-center m-mint-modal--header">
                        <div class="fix"></div>
                        <div class="m-mint-modal--close" @click="closeModal">
                            <Icon :inline="true" icon="ep:close-bold" color="white" width="15" />
                        </div>
                    </div>
                    <div v-if="modal.visible === 1">
                        <div class="m-mint-modal--wallet-title" @click="selectWallet">
                            {{ $t("select_wallet") }}
                        </div>
                        <div>
                            <div class="row-center m-mint-modal--wallet-item" @click="connectMeta">
                                <img class="m-mint-modal--wallet-icon" :src="images.iconMetaMask" alt="" />
                                <div class="m-mint-modal--wallet-label">Metamask</div>
                            </div>
                        </div>
                    </div>
                    <div v-if="modal.visible > 1">
                        <div class="m-mint-modal--alert-title">
                            {{ modal.title }}
                        </div>
                        <img class="m-mint-modal--alert-icon" :src="modalStatusIcon" />
                        <div class="m-mint-modal--alert-text">
                            {{ modal.message }}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import mseaIcon from '@/assets/images/msea-icon.png'
import mseaLogo from '@/assets/images/msea-logo.png'
import iconGithub from '@/assets/images/icon/social-github.png'
import iconTwitter from '@/assets/images/icon/social-twitter.png'
import iconFisher from '@/assets/images/icon/social-fisher.png'
import iconEmail from '@/assets/images/icon/social-email.png'
import iconFailed from '@/assets/images/icon/modal-fail.png'
import iconSuccess from '@/assets/images/icon/modal-success.png'
import iconMetaMask from '@/assets/images/icon/metamask.png'
import { Icon } from '@iconify/vue2';

export default {
    components: {
        Icon,
    },
    data() {
        return {
            images: {
                mseaIcon,
                mseaLogo,
                iconGithub,
                iconTwitter,
                iconFisher,
                iconEmail,
                iconFailed,
                iconSuccess,
                iconMetaMask
            },
            tokensMinted: 1355,
            tokensAmount: 10000,
            userLimit: 2,
            account: '',
            // account: '0x3aad5a3221822453d4a38906cd54aae12c5e455e',
            stage: 0, // 0 on sell, 1 sold out, 2 finished
            type: 1,
            userAmount: 0,
            confirmFinish: 2,
            modal: {
                visible: 0,
                title: '',
                message: ''
            },
            msgList: {}
        }
    },
    computed: {
        tokensMintedPercent: function () {
            return (this.tokensMinted / this.tokensAmount * 100).toFixed(2)
        },
        modalStatusIcon: function () {
            return this.modal.visible === 2 ? this.images.iconSuccess : this.images.iconFailed
        },
        getAccount() {
            if (this.account) {
                return this.account.substring(0, 6) + '....' + this.account.substring(this.account.length - 6, this.account.length)
            } else {
                return ''
            }
        },
    },
    mounted() {
        this.msgList = {
            // clover wallert don't exist
            CLOVER_ERROR: {
                message: this.$t("install_clover"),
                title: "",
                visible: 3,
            },
            // metamask wallert don't exist
            METAMASK_ERROR: {
                message: this.$t("install_metamask"),
                title: "",
                visible: 3,
            },
            NETWORK_ERROR: {
                message: this.$t("switch_to_miannet"),
                title: "",
                visible: 3,
            },
            // Exceed MINT Limit
            EXCEED_LIMIT: {
                message: this.$t("limit_info"),
                title: this.$t("purchase_un_success"),
                visible: 3,
            },
            INVALID_WALLET: {
                message: this.$t("invalid_wallet"),
                title: this.$t("purchase_un_success"),
                visible: 3,
            },
            TRY_AGAIN: {
                message: this.$t("try_again"),
                title: this.$t("purchase_un_success"),
                visible: 3,
            },
            WHITELIST_FINISHED: {
                message: this.$t("whitelist_finished"),
                title: this.$t("purchase_un_success"),
                visible: 3,
            },
            INSUFFICIENT_FUNDS: {
                message: this.$t("insufficient_funds"),
                title: this.$t("purchase_un_success"),
                visible: 3,
            },
            SUCCESS: {
                message: "",
                title: this.$t("purchase_success"),
                visible: 2,
            },
        }
    },
    methods: {
        calc(num) {
            if (this.userAmount <= 1 && num < 0) {
                return;
            }
            if (this.userAmount >= this.userLimit && num > 0) {
                return;
            }

            this.userAmount = Number(this.userAmount) + num
        },
        async goToOpensea() {
            // const url = await getOpenSeaUrl();
            // window.open(url)
        },
        closeModal() {
            this.modal.visible = 0
        },
        connectMeta() {
            console.log('connectMeta')
        },
        setInfo(msgName) {
            this.modal = this.msgList[msgName]
        },
        selectWallet() {
            this.modal.visible = 1;
        }
    }
}
</script>

<style scoped lang="scss">
.m-mint {
    @import './scss/m-nav.scss';
    @import './scss/m-banner.scss';
    @import './scss/m-modal.scss';
}
</style>